.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 500px;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

* {
  margin: 0;
  border: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-size: 100%;
  color: #fff;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

.replies {
  background-color: #5C54B9;
}

.replies .replies__inner {
  padding: 5% 0 0 30%;
  width: 100vw;
  height: 100vh;
}

.replies .replies__inner .replies__body {
  padding: 10px 0 0 40px;
  height: 100%;
}

.replies .replies__inner .replies__body .replies__title {
  margin-bottom: 50px;
}

.replies .replies__inner .replies__body .replies__title h2 {
  font-weight: 400;
  font-size: 2.66667rem;
  letter-spacing: 1px;
}

.replies .replies__inner .replies__body .replies__item {
  margin-bottom: 10px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 10px;
  width: 44%;
  font-weight: 400;
  font-size: 0.93333rem;
}

@media (max-width: 768px) {
  .replies .replies__inner .replies__body .replies__item {
    width: 70%;
  }
}

.replies .replies__inner .replies__body .replies__item .replies__data {
  margin-bottom: 5px;
}

.replies .replies__inner .replies__body .replies__item .replies__contacts {
  margin-bottom: 5px;
}

.replies .replies__inner .replies__body .replies__item .replies__message {
  line-height: 20px;
}

.replies .replies__inner .replies__pagination {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  width: 40%;
}

@media (max-width: 768px) {
  .replies .replies__inner .replies__pagination {
    width: 70%;
  }
}

.replies .replies__inner .replies__pagination .replies__btn {
  font-size: 1.06667rem;
  color: #fff;
}

.replies .replies__inner .replies__pagination .replies__count {
  font-size: 0.8rem;
}

.send {
  width: 100vw;
  height: 100vh;
}

.send .send__inner {
  padding-top: 30px;
  text-align: center;
}

.send .send__inner .send__title {
  margin-bottom: 2.66667rem;
}

.send .send__inner .send__title h2 {
  font-weight: 400;
  font-size: 3.46667rem;
}

.send .send__inner .send__description {
  margin: 0 auto 2.66667rem;
  width: 80%;
}

.send .send__inner .send__description p {
  font-size: 1.33333rem;
  line-height: 25px;
}

.send .send__inner .send___form {
  margin-bottom: 3.33333rem;
}

.send .send__inner .send__btns-group {
  display: flex;
  justify-content: space-around;
}

.send .send__inner .send__btns-group .send__btn {
  font-size: 1.06667rem;
  color: #fff;
}

.form {
  display: flex;
  flex-direction: column;
}

.form .form__label {
  position: relative;
}

.form .form__input {
  margin-bottom: 2.93333rem;
  border-radius: 5px;
  padding: 0 10px;
  width: 100%;
  height: 60px;
  font-size: 1.33333rem;
  text-align: center;
}

.form .form__input::placeholder {
  color: #ccc;
}

.form .form__input:focus {
  -webkit-box-shadow: 0 0 8px 7px rgba(46, 49, 51, 0.2);
  -moz-box-shadow: 0 0 8px 7px rgba(46, 49, 51, 0.2);
  box-shadow: 0 0 8px 7px rgba(46, 49, 51, 0.2);
}

.form .form__button {
  border-radius: 30px;
  height: 70px;
  font-size: 1.6rem;
  color: #ccc;
  opacity: 0.6;
  transition: opacity 0.7s;
}

.form .form__button:hover {
  opacity: 1;
}

.form .form__select {
  position: absolute;
  left: 0;
  top: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-left: 15px;
  width: 60px;
  height: 60px;
  font-size: 2.13333rem;
  cursor: pointer;
}

@media (max-width: 768px) {
  html {
    font-size: 12px;
  }
}
